.wp-block-hizzle-forms-forms {
	display: block;
	margin: 1rem auto;
	max-width: 600px;
	position: relative;

	&.is-style-hizzle-full {
		max-width: 100%;
	}

	&.is-style-hizzle-wide {
		max-width: 800px;
	}

	&.has-background {
		padding: 1.6rem;
	}

	.hizzle-forms-field {
		margin-bottom: 1.6rem;
	}

	.hizzle-forms__field-help-text {
		opacity: 0.8;
		font-size: 0.8rem;
		margin-top: 0.4rem;
		margin-bottom: 0.8rem;
		display: block;
	}
}
